.app {
  background: #f6f6f6;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.todo-list {
  display: flex;
  width: 400px;
  flex-direction: column;
  border-radius: 4px;
  max-height: 250px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.todo-item {
  display: flex;
  justify-content: space-between;
  background: #fff;
  border-radius: 5px;
  font-size: 16px;
  padding: 10px;
  margin-bottom: 6px;
}

.todo-text {
  width: 250px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.todo-input {
  width: 350px;
  border-radius: 5px;
  font-size: 16px;
  padding: 20px;
  outline: none;
  border: none;
  background: #f6f6f6;
  font-size: 16px;
}

.todo-btn {
  cursor: pointer;
  padding: 5px 10px;
  border: none;
  border-radius: 5px;
}

.todo-group-btn {
  display: flex;
  gap: 0.25rem;
}

.primary {
  background-color: yellowgreen;
  color: white;
}

.secondary {
  background-color: yellow;
  color: black;
}

.info {
  background-color: blue;
  color: white;
}

.warning {
  background-color: red;
  color: white;
}

.primaryBtn {
  margin: 20px 10px;
  cursor: pointer;
  font-weight: 500;
  padding: 13px 25px;
  border-radius: 15px;
  font-size: 0.8rem;
  border: none;
  color: white;
  background: #185adb;
  transition: all 0.25s ease;
}

.primaryBtn:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px -10px rgba(24, 90, 219, 0.6);
}